@import "../mixins";

.widget {
  flex: 1 0 50%;

  @include phone {
    flex: 1 0 100%;
  }

  &__header {
    margin-bottom: 2rem;
  }

  &__title {
    font-size: 1.8rem;
    font-weight: bolder;
  }
}

.widget-scenarios {
  &__content {
    background-color: rgba(200, 0, 0, 0.5);
    overflow-x: hidden;
  }
}

